* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Search */
.home-btn {
  background-color: transparent;
  color: blue;
  font-size: 2.5rem;
  padding: 1rem;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

.home-btn:hover {
  transform: scale(1.2);
  color: red;
  border: none
}

.search-container {
  height: 5rem;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  /* width: 100vw;
  position: fixed */
}

.search-container form {
  width: 90vw;
  max-width: 1120px;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.form-input {
  max-width: 500px;
  width: 500px;
  padding: 0.5rem 0.75rem;
  border-radius: 5px;
  transition: 0.1s;
}

.btn {
  background-color: #0000ff;
  border: transparent;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.5s;
  color: rgba(white);
  border-radius: 5px;
  width: 15%;
}

.btn:hover {
  color: gold;
  transform: translateY(-2px);
}




/* Favourites */
.favorites {
  padding: 1rem;
  display: flex;
  justify-content: center;
}

.favorites-content {
  width: 80vw;
  color: blue;
  font-size: 2rem;
  border-radius: 1rem;
  padding: 1rem;
  background-color: rgb(242, 242, 250);
}

.favorites-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;

}

.favorite-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.fav-img:hover {
  transform: scale(1.1);
}

.fav-img {
  margin-top: 1rem;
  width: 5rem;
  border: solid 0.2rem white;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.25s;
}

.remove-btn {
  background-color: transparent;
  border: none;
  margin-top: 1rem;
  cursor: pointer;
  transition: 0.5s;
  color: black;
}

.remove-btn:hover {
  color: rgb(255, 0, 0);
  transform: translateY(-2px);
}


/* Meals */
.section {
  padding: 3rem 0;
  width: 80vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-family: monospace;
  font-size: 35px;
  align-items: center;
}

.section-center {
  padding: 3rem 0;
  width: 90vw;
  max-width: 1120px;
  margin: 0 auto;
  display: grid;
  gap: 2rem;
}

@media screen and (min-width:776px) {
  .section-center {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width:992px) {
  .section-center {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.single-meal {
  background-color: rgb(218, 218, 237);
  color: blue;
  border-radius: 10px;
  box-shadow: 5px 5px 5 10 blue;
  transition: 0.5s;
}

.img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.single-meal img {
  height: 15rem;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
}

.single-meal h5 {
  padding: 0;
  margin: 0;
  font-size: 1.25rem;
}

.single-meal footer {
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.like-btn {
  background-color: transparent;
  border: transparent;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.5s;
  font-family: fantasy;
  color: red;
}

.like-btn:hover {
  color: rgb(255, 0, 0);
  transform: translateY(-2px);
}

.liked {
  color: black(255, 0, 0);
  background-color: transparent;
  border: transparent;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.5s;
  font-family: fantasy;
}

.liked:hover {
  color: rgb(255, 0, 0);
  transform: translateY(-2px);
}

/* Modal */

.modal-overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.85);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  transition: 0.5s;
  z-index: 100;
}

.modal-container {
  background-color: white;
  width: 80vw;
  height: 80vh;
  overflow: scroll;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}

.modal-img {
  height: 55vh;
  margin: 2rem 22rem;
  border-radius: 10px;
}

.modal-content {
  padding: 1rem 1.5rem;

}

.modal-content p {
  color: black;
  font-size: 1.25rem;
  font-family: cursive;
  padding: 1.5rem;
}

.modal-content a {
  display: block;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  text-decoration: underline;
}

.modal-content h4 {
  font-size: 2rem;
  margin-top: 5px;
  margin-bottom: 5px;
  color: blue;
  padding-left: 24px;
}

.close {
  color: red;
  text-align: center;
  font-size: 2.5rem;
}